.react-datepicker-popper {
    z-index: 3 !important;
}

.order-input {
    margin-bottom: 10px;
    min-width: 162px !important;

    label {
        min-width: 140px;
    }
}

.sidebar-nav {
    .nav-group, .nav-item {
        color: #fff;

        svg {
            color: #fff;
        }
        
        .nav-link {
            color:#fff;
            padding-left: 20px !important;

            svg {
                color: #fff;
            }
        }   
    }

    .nav-group .nav-group-items {
        padding-left: 20px;
    }
}

.sidebar-nav .nav-group-toggle::after {
    // background-image: url("/src/assets/images/svg/arrow-down-2-svgrepo-com.svg") !important;
    // background-image: url(../../public/arrow-down-2-svgrepo-com.svg) !important;
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 16 16%27 fill=%27rgba%280, 0, 0, 0.6%29%27%3e%3cpath fill-rule=%27evenodd%27 d=%27M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z%27/%3e%3c/svg%3e") !important;
}

.sidebar-nav .nav-group.show {
    background: var(--cui-sidebar-nav-group-bg);
}


.edit-inner {
    display: flex;
    gap: 10px;
}

.MuiBox-root,
.card-body {
    img {
        height: 150px;
        width: auto !important;
    }
}

.MuiBox-root {
    max-height: 100vh;
    overflow-y: auto;

    img  {
        margin-bottom: 1rem;
    }
}

.carousel-inner,
.carousel-item {
    max-height: 500px;

    img {
        object-fit: cover;
        object-position: center;
        max-height: 500px;
    }
}

.info-list {
    .card-body {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.media-menu {

    img {
        max-width: 150px;
        max-height: 150px;
        background-color: rgb(223, 223, 223);
    }
}

@media screen and (max-width: 1145px) { 
    .sum-inner {
        order: 4;
        min-width: 100%;
    }
    .card-body {
        table {
            td,tr,th {
                width: 100%;
                padding: 8px !important;
            }
            min-width: 270px;
        }
    }
}

@media screen and (max-width: 920px) { 
    .card-body {
        table {
            td,tr,th {
                padding: 8px !important;
            }
        }
    }
}

@media screen and (max-width: 800px) { 
    .css-1l9ohcm {
        width: 100%;
    }
}

a, h1, h2, h3, h4, h5 {
    color: rgb(26 94 51) !important;
}