/* orders/ */
@import url('styles/orders.css');
@import url('styles/products.css');
@import url('styles/useful-materials.css');
@import url('styles/materials.css');

/*================================================
input type="number" -      /
=================================================*/
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  -webkit-appearance: none;
          appearance: none;
  -moz-appearance: textfield;
}
/* ============================================== */

.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1.5vmin);
}

.App-link {
  color: #1D2024;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.characters {
  list-style: none;
  padding-left: 0;
}

.characters li {
  display: flex;
  align-items: center;
  border: solid 2px #d0d0d0;
  border-radius: .2em;
  padding: .5em .8em .5em .5em;
  margin-bottom: 1em;
}

.characters p {
  max-width: none;
  font-weight: bold;
  margin: 0;
}

.characters-thumb {
  overflow: hidden;
  flex-shrink: 0;
  width: 2em;
  height: 2em;
  background-color: #e8e8e8;
  padding: .5em;
  margin-right: .5em;
}

.characters-thumb img {
  display: block;
  width: 100%;
  height: auto;
}

.SortPunkt{
  margin-left: 40px;
}

/* .kramar-bg, .sidebar{
  background: rgb(3, 152, 158) !important;
} */
.sidebar-brand {
  min-height: 165px;
  overflow: hidden;
}

.sidebar img{
  position: relative;
  width: 100% !important;
  top: -5px;
  border-right: 1px solid #d8dbe0;
}


.btn-primary {
  --bs-btn-color: #1c1c1c !important;
  --bs-btn-hover-color: #6CB200 !important;
  --bs-btn-border-color: #1c1c1c !important;
  --bs-btn-hover-border-color: #6CB200 !important;
}

.accordion-body{
  background: #f8f9fa !important;
}

.myAllert {
  z-index: 9999999999;
  /* background-color: red; */
}

.header-brand .icon.icon-custom-size{
  display: none !important;
}

@media screen and (max-width: 767px) {
  .filter-field {
    margin-bottom: 10px;
  }
}
 
.accordion {
  --bs-accordion-active-color: #000 !important;
  --bs-accordion-active-bg: rgba(0, 0, 0, 0.03) !important;
  --bs-accordion-border-radius: 0 !important;
  --bs-accordion-btn-bg: rgba(0, 0, 0, 0.03) !important;
  /* --bs-accordion-btn-padding-x: 1.25rem; */
  /* --bs-accordion-btn-padding-x: 1.25rem; */
  /* --bs-accordion-btn-padding-y: 1rem; */
  --bs-accordion-btn-padding-y: 0.7rem !important;
}

.accordion-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.176) !important;
}


.accordion-body {
  background: #fff !important;
  /* padding: 0 !important; */

}

.accordion-button:focus {
  box-shadow: none !important;
}

.popover.bs-popover-end, .popover.bs-popover-top{
  z-index: 99999  !important;
}

.popover.bs-popover-top .popover-body{
  background-color: palegoldenrod;
}

.nav-pills .nav-link.active{
  border: 1px solid #03989e;
  background: white !important;
  border-radius: 0;
  font-weight: bold;

}

.MediaBox .btnTrashMin{
    border-radius: 60px;
    width: 30px;
    height: 30px;
    display: flex !important;
    align-items: center;
    justify-content: center;
    background: #fff;
    position: absolute;
    top:0;
    right: 0;
}
.MediaBox img{
  width: 100%;
}
.MediaBox{
  max-width: 300px;
}

.rowAnswerNew{
  background-color: #ffcdcd;
}
.rowAnswerOpened{
  background-color: #ffe6a5;
}
.rowAnswerSend{
  background-color: #d2ffd2;
}

.myForm__input-tel {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.375rem;
}
.myForm__input-tel:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
}

.react-confirm-alert-overlay {
  z-index: 1400; /*     MUI Modal */
}

.MuiModal-root {
  z-index: 1300; /*    MUI */
}

.form-check .form-check-input, .form-check-label {
  cursor: pointer;
}