.orders__linkToOrderProduct {
    text-decoration: underline;
    color: rgb(3, 152, 158);
    cursor: pointer;
}

.orders__linkToOrderProductIcon {
    color: rgb(3, 152, 158);
    cursor: pointer;
}
  
.orders-choose__table {
    width: 100%;
}

.orders-choose__th {
    font-size: 14px;
}

.orders-choose__th-img {
    width: 50px;
}

.orders-choose__th-price {
    width: 50px;
}

.orders-choose__th-count {
    width: 90px;
}
.orders-choose__TC {
    display: flex;
    flex-direction: column;
}

.orders-choose__th-action {
    display: block;
    width: 35px;
}

.orders-choose__mobile, .orders-choose__short-mobile {
    display: none;
}
/* .orders-choose__PC, .orders-choose__short-PC {
    display: block;
} */

@media (max-width: 1400px) {
    .orders-choose__th-img {
        width: 40px;
    }
}

@media (max-width: 1200px) {
    .orders-choose__mobile{
        display: block;
    }
    .orders-choose__PC
    /* , .orders-choose__short-PC  */
    {
        display: none;
    }
    .orders-choose__th-img {
        width: 30px;
    }
    .orders-choose__th-action {
        display: none;
    }
}

@media (max-width: 991px) {
    .orders-choose__th-action {
        display: block;
        width: 35px;
    }
}

@media (max-width: 576px) {
    .orders-choose__short-PC {
        display: none;
    }
    .orders-choose__short-mobile {
        display: block;
    }
    .orders-choose__th-img {
        display: none;
    }
    .orders-choose__th-action {
        display: none;
    }
    .orders-choose__cart {
        display: none;
        width: 35px !important;
    }
}

.orders-choose__img {
    width: 50px;
}

.orders-choose__td-name {
    max-width: 150px;
    overflow: hidden;
    /* font-size: 14px; */
    font-size: 24px;
}

.orders-choose__name {
    cursor: pointer;
    text-decoration: underLine;
    color: rgb(3, 152, 158);
}
  
.orders-choose__text {
    /* font-size: 14px; */
    font-size: 17px;
    min-width: 100px;
}

.orders-choose__count {
    padding-left: 5px !important;
    padding-right: 5px !important;
    /* width: 90px; */
    width: 120px;
    line-height: 17px; 
}

.orders-choose__count-balance_container {
    border: 1px solid #ccc;
    background: #f1eeee;
    padding-bottom: 10px;
    border-radius: 10px;
    position: absolute;
    /* top: 10px; */
    top: 45px;
    right: 5px;
}

.orders-choose__count-balance_title-container {
    border-bottom: 1px solid #ccc;
    position: relative;
    min-height: 40px;
    max-height: 65px;
    background-color: #d6d7d8f2;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    align-items: center;
    padding: 10px;
}


.orders-choose__count-balance_title {
    font-weight: 600;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.orders-choose__count-balance_title-productName {
    max-width: 90%;
    font-size: 12px;
    margin-top: 5px;
    font-weight: 400;
}

.orders-choose__count-balance_storages {
    position: relative;
    padding: 10px;
    /* margin-bottom: 5px; */
    min-height: 100px;
    max-height: 222px;
    overflow: auto;
}

.orders-choose__count-balance_storages ul {
    list-style: none;
    padding-left: 10px;
}

.orders-choose__count-balance_storages ul li {
    font-size: 12px;
    margin-bottom: 4px;
}

.orders-choose__count-balance__btn-show {
    color: rgb(3, 152, 158) !important;
    font-size: 14px;
    text-decoration: underline;
    cursor: pointer; 
}

.orders-choose__count-balance__btn-close {
    width: 25px;
    height: 25px;
    background-color: #6c757d;
    color: #fff;
    border: 1px solid transparent;
    border-radius: 5px;
    font-weight: 500;
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.orders-choose__count-balance__btn-close:hover {
    background-color: red;
    color: #000;
    border-color: #000;
}
.orders-choose__count-balance__btn-close:active {
    transform: scale(0.9);
}

.orders-choose__count-balance__btn-show:hover {
    cursor: pointer;
}

.orders-choose__count-1r {
    /* width: 90px; */
    width: 120px;
    margin-bottom: 5px;
}
.orders-choose__count-2r {
    /* width: 90px; */
    width: 120px;
    display: flex;
    justify-content: space-between;
}

.orders-choose__count-input {
    /* width: 90px; */
    width: 120px;
    font-size: 14px;
}

.orders-choose__count-button {
    /* width: 40px; */
    width: 50px;
    font-size: 14px;
}

.orders-choose__cart-mobile {
    display: none;
}

@media (max-width: 1400px) {
    .orders-choose__img {
        width: 40px;
    }
}

@media (max-width: 1200px) {
    .orders-choose__count-1r {
        width: 90px;
    }
    .orders-choose__count-2r {
        width: 90px;
    }
    .orders-choose__count-input {
        width: 90px;
    }
    .orders-choose__count-button {
        width: 40px;
    }
    
    /* .orders-choose__cart {
        display: none;
    } */
    .orders-choose__img {
        width: 30px;
    }
    .orders-choose__text {
        min-width: 80px;
    }
}

@media screen and (max-width: 991px) {
    .orders-choose__td-name {
        font-size: 16px;
    }
    .orders-choose__text {
        min-width: 115px;
        font-size: 16px;
    }

    .orders-choose__count-1r {
        width: 120px;
    }
    .orders-choose__count-2r {
        width: 120px;
    }
    .orders-choose__count-input {
        width: 120px;
    }
    .orders-choose__count-button {
        width: 50px;
    }

    .orders-choose__th-action {
        display: block;
    }
    .orders-choose__cart {
        display: block;
    }

    .orders-choose__count-balance_container {
        top: auto;
        bottom: 5px;
        right: 5px;
    }
}

@media (max-width: 576px) {
    .orders-choose__cart {
        display: none;
    }
    .orders-choose__td-img {
        display: none;
    }
    .orders-choose__th-action, .orders-choose__cart {
        display: none;
        padding: 3px !important;
    }
    .orders-choose__td-name, .orders-choose__text {
        font-size: 14px;
        min-width: 60px;
    }

    .orders-choose__count-1r {
        width: 90px;
    }
    .orders-choose__count-2r {
        width: 90px;
    }
    .orders-choose__count-input {
        width: 90px;
    }
    .orders-choose__count-button {
        width: 40px;
    }

    .orders-choose__cart-mobile {
        display: block;
        margin-top: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 90px;
        /* width: 120px; */
    }
}

@media screen and (max-width: 448px) {  
    .orders-choose__td-name, .orders-choose__text {
        font-size: 13px;
    }

    .orders-choose__count-1r, .orders-choose__count-2r {
        width: 100%;
    }

    .orders-choose__count-input {
        width: 100%;
    }
    
    .orders-choose__count-button {
        width: 100%;
    }
  }


